            </div></el-col
          >
          <el-col :span="8"
            ><div class="text item">
              <span class="label">当日兑换总额：</span><span class="value">{{ amount_exchange_token['today'] }}</span>
            </div></el-col
          >
          <el-col :span="8"></el-col>
        </el-row>

        <el-row>
          <el-col :span="8"
            ><div class="text item">
              <span class="label">尊享累计兑换积分总额：</span
              ><span class="value">{{ amount_exchange_by_debt['total']['zx'] }}</span>
            </div></el-col
          >
          <el-col :span="8"
            ><div class="text item">
              <span class="label">尊享当日兑换积分总额：</span
              ><span class="value">{{ amount_exchange_by_debt['today']['zx'] }}</span>
            </div></el-col
          >
          <el-col :span="8"></el-col>
        </el-row>

        <el-row>
          <el-col :span="8"
            ><div class="text item">
              <span class="label">普惠供应链累计兑换积分总额：</span
              ><span class="value">{{ amount_exchange_by_debt['total']['ph'] }}</span>
            </div></el-col
          >
          <el-col :span="8"
            ><div class="text item">
              <span class="label">普惠供应链当日兑换积分总额：</span
              ><span class="value">{{ amount_exchange_by_debt['today']['ph'] }}</span>
            </div></el-col
          >
          <el-col :span="8"></el-col>
        </el-row>
      </el-card>
      <el-card class="box-card">
        <div slot="header" class="clearfix title">
          <span>出清人数统计信息（截止到昨日）</span>
        </div>
        <el-row>
          <el-col :span="8"
            ><div class="text item">
              <span class="label">累计出清人数：</span><span class="value">{{ cleared_person_numbers['total'] }}</span>
            </div></el-col
          >
          <el-col :span="8"
            ><div class="text item">
              <span class="label">昨日出清人数：</span
              ><span class="value">{{ cleared_person_numbers['yesterday'] }}</span>
            </div></el-col
          >
          <el-col :span="8"></el-col>
        </el-row>
      </el-card>
      <el-card class="box-card">
        <div slot="header" class="clearfix title">
          <span>订单统计信息</span>
