        <el-row>
          <el-col :span="8"
            ><div class="text item">
              <span class="label">累计订单消耗积分：</span
              ><span class="value">{{ tokens_of_goods_sold['total'] }}</span>
            </div></el-col
          >
          <el-col :span="8"
            ><div class="text item">
              <span class="label">当日订单消耗积分：</span
              ><span class="value">{{ tokens_of_goods_sold['today'] }}</span>
            </div></el-col
          >
          <el-col :span="8"></el-col>
        </el-row>
      </el-card>
      <el-card class="box-card">
        <div slot="header" class="clearfix title">
          <span>商品统计信息</span>
        </div>

        <el-row>
          <el-col :span="8"
            ><div class="text item">
              <span class="label">已上架商品：</span
              ><span class="value">{{ goods_data['quantity_of_goods_on_sale'] }}</span>
            </div></el-col
          >
          <el-col :span="8"
            ><div class="text item">
              <span class="label">已下架商品：</span
              ><span class="value">{{ goods_data['quantity_of_goods_on_off_sale'] }}</span>
            </div></el-col
          >
          <el-col :span="8"
            ><div class="text item">
              <span class="label">已售罄商品：</span
              ><span class="value">{{ goods_data['quantity_of_goods_on_sold_out'] }}</span>
            </div></el-col
          >
