        <el-row>
          <el-col :span="8"
            ><div class="text item">
              <span class="label">待审核商品：</span
              ><span class="value">{{ goods_data['quantity_of_goods_on_checking'] }}</span>
            </div></el-col
          >
          <el-col :span="8"
            ><div class="text item">
              <span class="label">审核通过商品：</span
              ><span class="value">{{ goods_data['quantity_of_goods_on_checked'] }}</span>
