          <el-col :span="8"
            ><div class="text item">
              <span class="label">累计销售商品数量：</span
              ><span class="value">{{ goods_data['quantity_of_goods_sold'] }}</span>
            </div></el-col
          >
          <el-col :span="8"
            ><div class="text item">
              <span class="label">当日销售商品数量：</span
              ><span class="value">{{ goods_data['quantity_of_goods_sold_today'] }}</span>
