      <el-card class="box-card">
        <div slot="header" class="clearfix title">
          <span>商家统计信息</span>
        </div>
        <el-row>
          <el-col :span="8"
            ><div class="text item">
              <span class="label">累计入驻商家数量：</span><span class="value">{{ shops_data['shops_total'] }}</span>
            </div></el-col
          >
          <el-col :span="8"></el-col>
          <el-col :span="8"></el-col>
        </el-row>
      </el-card>
