  </div>
</template>

<script>
import { getMainOperateData, getMainDataFromWX } from '../../api/dataDisplay'
export default {
  name: 'MainOperateData',
  data() {
    return {
      user_regist: {
        // 注册用户数
        total: 0,
        today: 0
      },
      user_agree: {
        // 同意积分兑换协议人数
        total: 0,
        today: 0
      },
      times_exchange: {
        // 兑换积分次数
        total: 0,
        today: 0
      },
      user_exchange_token: {
        // 兑换积分人数
        total: 0,
        today: 0,
        todayNew: 0
      },
      user_exchange_by_debt: {
        // 尊享 / 普惠供应链 兑换积分人数
        total: { ph: 0, zx: 0 },
        today: { ph: 0, zx: 0 }
      },
      amount_exchange_token: {
        // 兑换积分总额
        total: 0,
        today: 0
      },
      amount_exchange_by_debt: {
        // 尊享 / 普惠供应链 兑换积分总额
        total: { ph: 0, zx: 0 },
        today: { ph: 0, zx: 0 }
      },
      cleared_person_numbers: {
        total: 0,
        yesterday: 0
      },
      tokens_of_goods_sold: {
        // 订单消耗积分
        total: 0,
        today: 0
      },
      goods_data: {
        // 商品统计信息
        quantity_of_goods_on_sale: 0, // 上架中商品总量
        quantity_of_goods_on_off_sale: 0, // 已下架商品总量
        quantity_of_goods_on_sold_out: 0, // 已售罄商品总量
        quantity_of_goods_on_checking: 0, // 审核中商品总量
        quantity_of_goods_on_checked: 0, // 审核已通过商品总量
        quantity_of_goods_on_check_fail: 0, // 审核失败商品总量
        quantity_of_goods_sold: 0, // 累积销售商品总量
        quantity_of_goods_sold_today: 0 // 当日销售商品总量
      },
      shops_data: {
        // 商家统计信息
        shops_total: 0 // 入驻商家数量
      },
      loading: null
    }
  },
  created() {
    this.getData()
  },
  methods: {
    getData() {
      const p1 = function() {
        return new Promise((resolve, reject) => {
          getMainOperateData().then(
            res => {
              resolve(res)
            },
            err => {
              resolve(err)
            }
          )
        })
      }

      const p2 = function() {
        return new Promise((resolve, reject) => {
          getMainDataFromWX().then(
            res => {
              resolve(res)
            },
            err => {
              resolve(err)
            }
          )
        })
      }
      this.loading = this.$loading({
        lock: true,
        text: 'Loading',
        background: 'rgba(0, 0, 0, 0.6)'
      })
      Promise.all([p1(), p2()])
        .then(res => {
          if (!res[0].errorCode) {
            const {
              total_reg,
              today_reg,
              total_agree_people,
              today_agree_people,
              total_exchange_num,
              today_exchange_num,
              total_exchange_people,
              today_exchange_people,
              new_exchange_people,
              total_exchange_amount,
              today_exchange_amount,
              total_integral,
              today_integral,
              sale_on,
              sale_off,
              sold_out,
              is_check_ing,
              is_check_on,
              is_check_off,
              total_number,
              today_number,
              suppliers
            } = res[0]
            this.user_regist.total = total_reg
            this.user_regist.today = today_reg
            this.user_agree.total = total_agree_people
            this.user_agree.today = today_agree_people

            this.times_exchange.total = total_exchange_num
            this.times_exchange.today = today_exchange_num

            this.user_exchange_token.total = total_exchange_people
            this.user_exchange_token.today = today_exchange_people
            this.user_exchange_token.todayNew = new_exchange_people

            this.amount_exchange_token.total = total_exchange_amount
            this.amount_exchange_token.today = today_exchange_amount

            this.cleared_person_numbers.total = ''
            this.cleared_person_numbers.yesterday = ''

            this.tokens_of_goods_sold.total = total_integral
            this.tokens_of_goods_sold.today = today_integral

            // 商品统计信息
            this.goods_data = {
              ...this.goods_data,
              ...{
                quantity_of_goods_on_sale: sale_on, // 上架中商品总量
                quantity_of_goods_on_off_sale: sale_off, // 已下架商品总量
                quantity_of_goods_on_sold_out: sold_out, // 已售罄商品总量
                quantity_of_goods_on_checking: is_check_ing, // 审核中商品总量
                quantity_of_goods_on_checked: is_check_on, // 审核已通过商品总量
                quantity_of_goods_on_check_fail: is_check_off, // 审核失败商品总量
                quantity_of_goods_sold: total_number, // 累积销售商品总量
                quantity_of_goods_sold_today: today_number // 当日销售商品总量
              }
            }

            this.shops_data.shops_total = suppliers
          } else {
            this.$message.error(res[0].errorMsg)
          }

          if (!res[1].errorCode) {
            // data from wx
            this.cleared_person_numbers.total = res[1].data.total_quite_number
            this.cleared_person_numbers.yesterday = res[1].data.yesterday_quite_number
            this.user_exchange_by_debt.today.ph = res[1].data.ph_today_exchange_user
            this.user_exchange_by_debt.today.zx = res[1].data.zx_today_exchange_user
            this.user_exchange_by_debt.total.ph = res[1].data.ph_total_exchange_user
            this.user_exchange_by_debt.total.zx = res[1].data.zx_total_exchange_user
            this.amount_exchange_by_debt.today.ph = res[1].data.ph_today_exchange_num
            this.amount_exchange_by_debt.today.zx = res[1].data.zx_today_exchange_num
            this.amount_exchange_by_debt.total.ph = res[1].data.ph_total_exchange_num
            this.amount_exchange_by_debt.total.zx = res[1].data.zx_total_exchange_num
          } else {
            this.$message.error(res[1].errorMsg)
          }
        })
        .finally(() => {
          this.loading.close()
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.clearfix:before,
.clearfix:after {
  display: table;
  content: '';
}
.clearfix:after {
  clear: both;
}
.box-card {
  font-size: 14px;
  margin-bottom: 20px;
}
.item-line {
  border-bottom: 1px solid #ebeef5;
  margin-bottom: 10px;
}
.title {
  font-weight: bold;
}
.item {
  line-height: 30px;

  .label {
    display: inline-block;
    width: 210px;
    text-align: right;
  }
}
.header {
  margin-bottom: 10px;
}
.data-wrapper {
}
</style>
